@import '../../../colors/colors.scss';

.Button {
  box-sizing: border-box;
  border: 0;
  padding: 0;
  border-radius: 8px;
  height: 48px;
  padding-left: 48px;
  padding-right: 48px;
  outline: none;
  position: relative;
  width: fit-content;
  cursor: pointer;

  &-Spinner {
    position: absolute;
    top: 8px;
    left: calc(50% - 16px);
    width: 24px;
    height: 24px;
    margin: 0 auto;
    border: 4px solid;
    border-radius: 50%;
    border-color: transparent rgba(255, 255, 255, 0.9) rgba(255, 255, 255, 0.9);
    animation: cssload-spin 690ms infinite linear;
  }

  &-Text {
    color: white;
    box-sizing: border-box;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 48px;
    text-align: center;

    &_loading {
      opacity: 0;
    }
  }

  &_loading {
    background: linear-gradient(90deg, #0EC261 2.61%, #039F67 112.6%);
  }

  &_disabled {
    background-color: $GrayLight;
  }

  &_decline {
    background: linear-gradient(90deg, #493013 0%, #7B0C3B 100%);
  }

  &_default {
    background: linear-gradient(90deg, #0EC261 2.61%, #039F67 112.6%);

    &:hover,
    &:focus {
      background: linear-gradient(90deg, #0B934A 2.61%, #026E47 112.6%);
    }

    &:active {
      background: linear-gradient(90deg, #076432 2.61%, #013C27 112.6%);
    }


  }

  &_main {
    @extend .Button_default;

    @media (max-width:767px) {
      border-radius: 0;
      width: 100%;
    }
  }

  &_slider {
    border-radius: 4px;

    &_index {

      &_0 {
        background: linear-gradient(90deg, #13493F 0%, #0C7B1B 100%);
      }

      &_1 {
        background: linear-gradient(90deg, #132949 0%, #0C7B67 100%);
      }

      &_2 {
        background: linear-gradient(90deg, #493013 0%, #7B0C3B 100%);
      }

      &_3 {
        background: linear-gradient(90deg, #281349 0%, #720C7B 100%);
      }
    }
  }
}

@keyframes cssload-spin {
  100% {
    transform: rotate(360deg);
    transform: rotate(360deg);
  }
}